.argument_flex-div {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    background: #FDF6D1;
    padding: 2rem;
    h2 {
        color: #FFA72B;
    }
    @media (min-width: 700px) {
        justify-content: space-between;
    }
}

.outerDiv {
    display: flex;
    flex-direction: column;
    @media (min-width: 700px) {
        flex-direction: row;
    }
}

.top-image {
    width: 100%;
    height: 300px;
    object-fit: cover;
    @media (min-width: 700px) {
        width: 45%;
        height: 300px;
    }
}

.arrow-flex {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;

}

.blackLink {
    width: 100%;
    color: black !important;
    appearance: none !important;
    &:hover {
        color: black !important;
    }
}

.noMargin {
    margin: 0;
    margin-right: 1rem;

}